import React from "react";
import PropTypes from "prop-types";
import classNames from "classnames";
import {
  FacebookShareButton,
  FacebookIcon,
  LinkedinShareButton,
  LinkedinIcon,
  TwitterShareButton,
  TwitterIcon,
} from "react-share";


import GridContainer from "@components/Grid/GridContainer.js";
import GridItem from "@components/Grid/GridItem.js";

import BlogSideNavSimilarByTag from "@components/layout/blog/sideNav/sections/similarTag.js";
import BlogSideNavSeriesNode from "@components/layout/blog/sideNav/sections/series.js";
import BlogSideNavMysticApes from "@components/layout/blog/sideNav/sections/mysticApes.js";

import Card from "@components/card/card.js";
import CardBody from "@components/card/cardBody.js";
// import CardFooter from "@components/card/cardFooter.js";
// import CardHeader from "@components/card/cardHeader.js";


BlogSideNav.propTypes = {
    props: PropTypes.object,
}

export default function BlogSideNav(props) {

    // const { posts } = useStaticQuery(
    //     graphql`
    //         query {
    //             posts: allThreeMysticApesPost(filter: {showNode: {eq: true}}, sort: {fields: post___startDate, order: DESC}, limit: 10) {
    //                 edges {
    //                     node {
    //                         post {
    //                             title
    //                             excerpt
    //                             mysticApes
    //                             series {
    //                                 name
    //                                 part
    //                             }
    //                             startDate
    //                             gridSmall {
    //                                 publicURL
    //                             }
    //                             gridLarge {
    //                                 publicURL
    //                             }
    //                         }
    //                         slug
    //                     }
    //                 }
    //             }
    //         }
    //     `
    // )
    const { pageNode, classes, classesPost, className } = props;
    const themeVariation = (typeof window !== 'undefined') ? (localStorage.getItem('themeVariation') ? localStorage.getItem('themeVariation') : "dark") : "dark";


    const navClasses = classNames({
        [classesPost.blogSideNav]: true,
        [themeVariation]: true,
        [className]: className
    });

    const seriesNode = pageNode.seriesNode !== null ? pageNode.seriesNode : [];
    const similarNodeByTag = pageNode.similarNodeByTag !== null ? pageNode.similarNodeByTag : [];

    return (
        <nav className={navClasses}>
            <GridContainer justify="center">
                <GridItem cs={12} sm={12} md={12}>
                    <Card>
                        <CardBody className={classes.cardBody}>
                            <div>
                                <FacebookShareButton hashtag={pageNode.postHash.join(" ")} url={`https://www.threemysticapes.com${pageNode.slug}`}>
                                    <FacebookIcon size={32} round={true} />
                                </FacebookShareButton>
                                <LinkedinShareButton title={pageNode.context.title} source={`threemysticapes.com`} summary={pageNode.context.excerpt} url={`https://www.threemysticapes.com${pageNode.slug}`}>
                                    <LinkedinIcon size={32} round={true} />
                                </LinkedinShareButton>
                                <TwitterShareButton title={pageNode.context.title} hashtags={pageNode.postHash} url={`https://www.threemysticapes.com${pageNode.slug}`}>
                                    <TwitterIcon size={32} round={true} />
                                </TwitterShareButton>
                            </div>
                            <BlogSideNavSeriesNode pageNode={pageNode} navClasses={navClasses} classes={classes} classesPost={classesPost}/>
                            <BlogSideNavSimilarByTag pageNode={pageNode} navClasses={navClasses} classes={classes} classesPost={classesPost} excludeNodes={seriesNode.map((node)=>node.id)}   />
                            <BlogSideNavMysticApes pageNode={pageNode} navClasses={navClasses} classes={classes} classesPost={classesPost} excludeNodes={seriesNode.map((node)=>node.id).concat(similarNodeByTag.map((node)=>node.id)) }   />
                        </CardBody>
                    </Card>
                </GridItem>
            </GridContainer>
        </nav>

    );
}
