import React from "react"
import { graphql, Link } from "gatsby"

import { MDXProvider } from "@mdx-js/react"
import { MDXRenderer } from "gatsby-plugin-mdx"


import LayoutBlog from "@components/layout/layoutBlog"

// MDX Components
import { H1, H2, H3, H4, H5, H6 } from "@components/header/header.js";
import Button from "@components/customButtons/button.js";
import GridContainer from "@components/Grid/GridContainer.js";
import GridItem from "@components/Grid/GridItem.js";
import GridList from '@material-ui/core/GridList';
import GridListTile from '@material-ui/core/GridListTile';
import GridListTileBar from '@material-ui/core/GridListTileBar';
import Modal from '@material-ui/core/Modal';


// Provide common components here
const shortcodes = { Modal, GridList, GridListTile, GridListTileBar, GridContainer, GridItem, Button, Link, H1, H2, H3, H4, H5, H6 }

/*
children: 
location: {hash:, host:, hostname:, href, key, orgin, pathname, port, protocol, search, state:{key:}}
navigate: ƒ navigate(to, options)
pageContext: {slug: }
pageResources: {json: {…}, page: {…}, staticQueryResults: {…}, component: ƒ}
params: {}
path: "/about"
pageContext: {slug: "/about"} //Based off gatsby-node create page
uri: "/about"
*/
export default function TemplatePost({ data, pageContext, location, ...props }) {
  
  const { node } = data  

  return (
    <LayoutBlog pageNode={node} location={location} pageContext={pageContext} {...props}>
      <MDXProvider components={shortcodes}>
        <MDXRenderer pageNode={node} pageContext={pageContext} location={location}>{pageContext.body}</MDXRenderer>
      </MDXProvider>
    </LayoutBlog>
  )
}

// node:threeMysticApesPost(post: {slug: {eq: $rawSlug}}) {
export const query = graphql`
  query($postID: String!) {
    node: threeMysticApesPost(id: { eq: $postID }) {
      id
      postHash
      slug
      readingTime {
        text
        minutes
        time
        words
      }
      context: post {
        title
        description
        hashTag
        meta {
          content
          name
          property
        }
        mysticApes {
          name
        }
        seriesData {
          series {
            name
          }
          part
        }
        pageStyle {
          name
          style
        }
        hero {
          publicURL
        }
        startDate
        stylePath {
          relativePath
        }
        contentImages {
          name
          images {
            title
            image {
              publicURL
            }
          }
        }
      }
      seriesNode {
        id
        slug
        post {
          title
          startDate
          ariaLabel
          excerpt
          seriesData {
            series {
              name
            }
            part
          }
        }
      }
      similarNodeByTag {
        id
        slug
        post {
          title
          startDate
          ariaLabel
          excerpt
        }
      }
      similarNodeByMysticApes {
        id
        slug
        post {
          title
          startDate
          ariaLabel
          excerpt
        }
      }
    }
  }
`