import React from "react"
import classNames from "classnames";
import { makeStyles } from "@material-ui/core/styles";
import PropTypes from "prop-types";

import Moment from 'moment';

import "fontsource-roboto"
//@FontAwsome
import { library } from '@fortawesome/fontawesome-svg-core'
import { fab } from '@fortawesome/free-brands-svg-icons'
// import { faCheckSquare, faCoffee } from '@fortawesome/free-solid-svg-icons'

//templateComponents
import LazyHero from 'react-lazy-hero';
import LayoutMain from "@components/layout/layoutMain.js";
import PostHeroText from "@components/layout/section/common/blog/postHeroText.js";
import PageMaincontent from "@components/layout/section/common/page/mainContent.js";
import BlogSideNav from "@components/layout/blog/sideNav/side.js";

// core components
import GridContainer from "@components/Grid/GridContainer.js";
import GridItem from "@components/Grid/GridItem.js";

import { layoutPageStyle } from "assets/jss/views/layout/layout.js";
import { globalPageStyle } from "assets/jss/common/globalPage.js"
import { layoutPostStyle } from "assets/jss/template/blog/post.js";

library.add(fab);

const useStyles = makeStyles(layoutPageStyle);
const useStylesPage = makeStyles(globalPageStyle);
const useStylesPost = makeStyles(layoutPostStyle);


LayoutBlog.propTypes = {
  children: PropTypes.node.isRequired,
  pageNode: PropTypes.object.isRequired,
  props: PropTypes.object,
}


function GetHeroClassNames(pageNode, classes) {
  const heroClassNames = [classes.hero];
  if (pageNode.context.pageStyle === null) {
    return (heroClassNames)
  }

  if (pageNode.context.pageStyle[0].name === "hero") {
    if (pageNode.context.pageStyle[0].style === null) {
      return (heroClassNames)
    }
    return (heroClassNames.concat(pageNode.context.pageStyle[0].style));
  }
  for (let index = 0; index < pageNode.pageStyle.length; index++) {
    if (pageNode.context.pageStyle[index].name === "hero") {
      return (heroClassNames.concat(pageNode.context.pageStyle[index].style));
    }
  }

  return heroClassNames

}

export default function LayoutBlog({ children, pageNode, location, pageContext, ...props }) {

  const classes = useStyles();
  const classesPage = useStylesPage();
  const classesPost = useStylesPost();

  const themeVariation = (typeof window !== 'undefined') ? (localStorage.getItem('themeVariation') ? localStorage.getItem('themeVariation') : "dark") : "dark";  
  if (typeof window !== 'undefined'){
    localStorage.setItem("themeVariation", themeVariation);
  }

 

  return (
    <LayoutMain pageNode={pageNode} classes={classes} classesPage={classesPage} classesPost={classesPost} location={location} pageContext={pageContext}{...props}>
      <LazyHero className={classNames(GetHeroClassNames(pageNode, classesPost))} imageSrc={pageNode.context.hero === null ? null : pageNode.context.hero.publicURL}>
        <div className={`heroText`}>
          <PostHeroText classes={classes} classesPage={classesPage} classesPost={classesPost} pageNode={pageNode}/>
        </div>
      </LazyHero>
      <GridContainer justify="center">
        <GridItem xs={12} sm={12} md={12}>
          <GridContainer justify="center">
            <PageMaincontent pageNode={pageNode} classes={classes} classesPage={classesPage} classesPost={classesPost} location={location} pageContext={pageContext}>
              <div>
                  {pageNode.readingTime.text}
                  <br/>
                    <div>{Moment(pageNode.context.startDate).format('MMMM DD, YYYY')}</div>
              </div>
              {children}
            </PageMaincontent>
            <BlogSideNav  pageNode={pageNode} classes={classes} classesPage={classesPage} classesPost={classesPost}/>
          </GridContainer>
        </GridItem>
      </GridContainer>
    </LayoutMain>
  )
}